<template>
  <div class="template-container">
    <div class="template-container-title">编辑计划</div>

    <el-form ref="form" :model="form">
      <!--推广目标 begin -->
      <div class="temp-item-block">
        <div class="temp-item-title">推广目标</div>
        <div class="temp-item-main">
          <el-radio-group v-model="product_type" class="temp-radio-group" disabled>
            <div class="temp-radio-item" v-for="(val, key)  in productTypeList">
              <el-radio :label="key" @change='campaignTypeChange(val.campaign_type)'>{{val.name}}</el-radio>
              <span class="temp-radio-remark">{{val.desc}}</span>
            </div>
          </el-radio-group>
        </div>
      </div>
      <!--推广目标 end -->
      <!--计划设置 begin -->
      <div class="temp-item-block">
        <div class="temp-item-title">计划设置</div>
        <div class="temp-item-main">
          <div class="temp-item-block">
            <label class="temp-item-label" for="">计划类型：</label>
            <el-radio-group v-model="campaign_type" disabled>
              <el-radio v-for="(val, key, index) in campaignTypeList" :key="key" :label="key">{{val.name}}</el-radio>
            </el-radio-group>
          </div>
          <div class="temp-item-block" v-show='campaign_type!="CAMPAIGN_TYPE_WECHAT_MOMENTS"'>
            <label class="temp-item-label" for="">日限额：</label>
            <div class="temp-item-inlineblock">
              <input class="temp-item-inputtext" v-model="form.daily_budget" type="text" value="" />
              <span class="temp-item-remark">元/天</span>
            </div>
          </div>
          <div class="temp-item-block">
            <label class="temp-item-label" for="">投放速度：</label>
            <el-radio-group v-model="form.speed_mode">
              <div class="radio-inlineblock" v-for="(val, key, index) in speedModeList">
                <el-radio :label="key">{{val}}</el-radio>
              </div>
            </el-radio-group>
          </div>
          <div class="temp-item-block">
            <label class="temp-item-label" for="">计划名称：</label>
            <div class="temp-item-inlineblock">
              <input class="temp-long-inputtext" @input="campaignNameChange" v-model="form.campaign_name" type="text" value="" maxlength="40" />
              <span class="temp-item-inlayremark">{{ campaign_name_size }}/40</span>
            </div>
          </div>
        </div>
      </div>
      <!--计划设置 end -->
      <el-form-item>
        <el-button type="primary" :loading="submitLoading" @click="onSubmit">确认修改</el-button>
        <el-button @click="onCancel">取消</el-button>
      </el-form-item>
    </el-form>
  </div>

</template>

<script>
export default {
  created: function() {
    var params = this.$route.params || {}
    if (params.hasOwnProperty('campaign_id')) {
      this.campaignId = params.campaign_id
    }
    if (this.account_id) {
      this.getCommonEnum()
    }
  },
  watch: {
    // 监听 account_id 发生变化
    account_id() {
      // console.log('account:', this.account_id)
    }
  },
  computed: {
    account_id() {
      var account = this.$store.getters.get_account_action
      return account.account_id
    }
  },
  data() {
    return {
      submitLoading: false, // 禁止重复提交 -- loading 状态
      campaignId: 0,
      productTypeList: [],
      campaignTypeConfig: {},
      campaignTypeList: {},
      speedModeList: {},
      campaign_name_size: 0,
      campaign_type: '',
      product_type: '',
      form: {
        campaign_id: '',
        // campaign_type: '',
        speed_mode: '',
        // product_type: '',
        daily_budget: '',
        campaign_name: ''
      }
    }
  },
  mounted() {
    //console.log(this.listData)
  },
  methods: {
    campaignTypeChange(type) {
      this.campaignTypeList = {}
      var campaign_type = this.form.campaign_type
      this.form.campaign_type = ''
      for (var key in type) {
        if (type[key] == campaign_type) {
          this.form.campaign_type = campaign_type
        }
        this.campaignTypeList[type[key]] = this.campaignTypeConfig[type[key]]
      }
    },
    onCancel() {
      this.$router.push('/advertising/campaign')
    },
    onSubmit() {
      var params = this.form
      var that = this
      params.account_id = this.account_id
      that.submitLoading = true
      this.$axios
        .get('/Admin/Marketing_Campaigns/update', {
          params: params
        })
        .then(function(response) {
          var respData = response.data
          that.submitLoading = false
          if (respData.code == 0) {
            that.$message({
              type: 'success',
              message: respData.msg
            })
            setTimeout(function() {
              that.$router.push('/advertising/campaign')
            }, 500)
          }else{
            that.$message.error(respData.msg)
          }
        })
        .catch(function(err) {
          that.$message('网络繁忙，请稍后再试！')
          that.submitLoading = false
          console.log(err)
        })
    },
    campaignNameChange(e) {
      this.campaign_name_size = this.form.campaign_name.length
    },
    getCommonEnum() {
      var that = this
      this.$axios
        .get('/Admin/Marketing_Common/getEnum', {})
        .then(function(response) {
          var respData = response.data
          if (respData.code == 0) {
            that.productTypeList = respData.data['PRODUCT_TYPE']
            var product_type_desc = respData.data['PRODUCT_TYPE_DESC']
            that.campaignTypeConfig = respData.data['CAMPAIGN_TYPE']
            if (that.campaignId == 0) {
              that.campaignTypeList = that.campaignTypeConfig
            } else {
              that.getCampaign(that.campaignId)
            }
            that.speedModeList = respData.data['SPEED_MODE']
            // for (var key in product_type) {
            //   var obj = new Object()
            //   obj.label = key
            //   obj.name = product_type[key]['name']
            //   obj.desc = product_type[key]['desc']
            //   obj.campaign_type = product_type[key]['campaign_type']
            //   that.productTypeList.push(obj)
            //  // that.form.product_type = key
            // }
          }
        })
        .catch(function(err) {
          console.log(err)
        })
    },
    getCampaign(campaign_id) {
      var that = this
      this.$axios
        .get('/Admin/Marketing_Campaigns/get', {
          params: {
            campaign_id: campaign_id,
            account_id: this.account_id
          }
        })
        .then(function(response) {
          var respData = response.data
          if (respData.code == 0) {
            if (respData.data.list[0]) {
              that.form.campaign_id = respData.data.list[0]['campaign_id']
              that.campaign_type = respData.data.list[0]['campaign_type']
              that.form.speed_mode = respData.data.list[0]['speed_mode']
              that.product_type = respData.data.list[0]['product_type']
              that.form.daily_budget =
                respData.data.list[0]['daily_budget'] / 100
              that.form.campaign_name = respData.data.list[0]['campaign_name']
              that.campaignTypeChange(
                that.productTypeList[that.product_type]['campaign_type']
              )
              that.campaignNameChange()
            }
          }
        })
        .catch(function(err) {
          console.log(err)
        })
    }
  }
}
</script>


<style scoped>
.temp-radio-item {
  width: 100%;
  margin: 0 0 20px 0;
}
.temp-radio-remark {
  display: inline-block;
  color: #999;
  font-size: 14px;
  margin-left: 100px;
}

.radio-inlineblock {
  display: inline-block;
  margin-left: 20px;
}
</style>



